import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { environment } from '../../../environments/environment';
import { CommonModule } from '@angular/common';
import { CookieHelper } from '../../shared/services/cookie.helper';
import { OrderStatus } from '../../shared/models/enums';
import { RouterLink } from '@angular/router';
import { URLS } from '../../shared/utils/urls';
import { HfCardComponent } from '@monorepo/ui';
import { dayIdFormatted } from "../../shared/utils/day-id-pipe";
import { Cookie } from '@monorepo/utils';

@Component({
  selector: 'app-orders',
  standalone: true,
  templateUrl: './orders.component.html',
  styleUrl: './orders.component.scss',
  imports: [RouterLink, CommonModule, HfCardComponent, dayIdFormatted]
})
export class OrdersComponent implements OnInit, OnDestroy {
  toOrderDetails: string = `/${URLS.ORDER_DETAILS}/`;
  orderStatus = OrderStatus;
  ordersApiData!: Array<object>;
  ordersApiSubscription: Subscription;
  isOrdersLoaded: boolean = false;

  constructor(private http: HttpClient, private cookie: CookieHelper) { }

  ngOnInit(): void {
    this.callOrderDetailsApi();
  }

  callOrderDetailsApi() {
    const ordersApi = `${environment.api_url}customer/order/all?userId=${this.cookie.getCookie(Cookie.USER_ID)}`;
    let header = {
      'Content-Type': 'application/json',
      'X-Api-Key': environment.api_key,
      'DAVOS-IsMobile': 'false',
      Authorization: `Bearer ${this.cookie.getCookie(Cookie.AUTH_TOKEN)}`,
    };
    this.ordersApiSubscription = this.http.get(ordersApi, { headers: header }).subscribe((res: Array<object>) => {
      this.ordersApiData = res;
      this.isOrdersLoaded = true;
    });
  }

  ngOnDestroy(): void {
    if (this.ordersApiSubscription) {
      this.ordersApiSubscription.unsubscribe();
    }
  }
}
